<template>
  <div ref="threeDom" class="three-container">
    <headComp />
    <ul class="map-container">
      <li
        v-for="item in mapList"
        :key="item.snum"
        @click="handleClick(item.snum)"
      >
        {{ item.sname }}
      </li>
    </ul>
  </div>
</template>
<script>
import { onMounted, onUnmounted, ref } from "vue";
import headComp from "../Home/Head/index";
import * as common from "../../common/three_Common.js";
import stageApi from "@/api/stage";
import { setLoadSNum } from "@/utils/auth";
import { useRouter } from "vue-router";
// ! note: setup  three 
export default {
  components: {
    headComp
  },
  setup() {
    const mapList = ref([]);
    const router = useRouter();

    const getAllMap = async () => {
      const params = {
        act: "bPxM1k2cWCeMeEIbWZV1586415800673"
      };
      const res = await stageApi.getAllScene(params);
      const { data } = res;
      mapList.value = data.filter(item => !item.sname.includes(""));
    };

    const handleClick = snum => {
      setLoadSNum(snum);
      router.push("/");
    };

    const threeDom = ref();

    const initCommon = () => {
      common.initScene(); //
      common.initCamera(); //
      const dom = common.initRenderer(); //
      common.initController(); //
      // common.initStats(); //
      // common.initAxis(); //
      common.loadModel(); //
      common.initOcean();
      common.registerEventListener("resize", common.onWindowResize);
      threeDom.value.appendChild(dom);
    };

    let animationId = null;

    const render1 = () => {
      common.controls.update();
      common.renderer.render(common.scene, common.camera);
      common.updateOcean();
      // common.labelRenderer.render(common.scene, common.camera);
      // common.stats.update();
      animationId = requestAnimationFrame(render1);
    };
    onMounted(() => {
      getAllMap();
      initCommon();
      render1();
    });

    onUnmounted(() => {
      common.cancelEventListener("resize", common.onWindowResize);
      cancelAnimationFrame(animationId);
    });

    return {
      threeDom,
      mapList,
      handleClick
    };
  }
};
</script>

<style lang="scss" scoped>
.three-container {
  position: relative;
  overflow: hidden;
  height: 100vh;
  background: url("../../assets/dashboard/3d/bg.png") repeat center center/100%
    100%;

  .map-container {
    position: absolute;
    bottom: 20%;
    right: 20px;
    background: #fff;
    padding: 20px 0;
    border-radius: 5px;
    border: 1px solid #ebeef5;
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
    > li {
      padding: 0 20px;
      line-height: 36px;
      color: #606266;
      cursor: pointer;
      font-size: 14px;
      &:hover {
        color: #66b1ff;
        background-color: #ecf5ff;
      }
    }
  }
}
</style>
