export function initRenderer() {
  //

  renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true }); //
  //
  renderer.getContext().getShaderInfoLog = () => "";
  renderer.getContext().getShaderInfoLog = () => "";
  renderer.gammaOutput = true;
  // renderer.outputEncoding = true;
  renderer.gammaFactor = 2.2; //  gammaFactor()2.2
  LinearToneMapping = THREE.LinearToneMapping;
  renderer.toneMapping = LinearToneMapping;
  renderer.shadowMap.enabled = true;
  renderer.shadowMap.type = THREE.PCFSoftShadowMap;
  renderer.toneMappingExposure = 1; // 
  renderer.physicallyCorrectLights = true;
  const pmremGenerator = new THREE.PMREMGenerator(renderer); // hdr
  pmremGenerator.compileEquirectangularShader();
  new RGBELoader()
    .setDataType(THREE.UnsignedByteType)
    .load("/HDR/railway_bridge_02_2k.hdr", function(texture) {
      const envMap = pmremGenerator.fromEquirectangular(texture).texture;
      envMap.isPmremTexture = true;
      pmremGenerator.dispose();
      // 
      scene.environment = envMap; // 
      // scene.background = envMap; // 
    });
  renderer.setSize(window.innerWidth, window.innerHeight);
  renderer.setPixelRatio(window.devicePixelRatio);
  // document.body.appendChild(renderer.domElement);

  //CSS2D
  // labelRenderer = new CSS2DRenderer();
  // labelRenderer.setSize(window.innerWidth, window.innerHeight);
  // labelRenderer.domElement.style.position = "absolute";
  // labelRenderer.domElement.style.top = 0;
  // labelRenderer.domElement.style.pointerEvents = "none";
  // document.body.appendChild(labelRenderer.domElement);
  return renderer.domElement;
}
