//()
export function removeObj(arr) {
  //
  if (arr instanceof Array) {
    if (arr.length > 0) {
      for (const index in arr) {
        scene.remove(arr[index]);
      }
    }
    arr.length = 0;
  } else {
    scene.remove(arr);
  }
}
